#register-form{
  width: 80%;
  margin: 0 auto;
  margin-top: 10px;
}
form #submit{
  margin-top: 10px;
  border: 0px solid transparent;
}
form #register{
  margin-top: 10px;
  margin-left: 10px;
  border: 0px solid transparent;
}
.error-message{
  color: red;
}
.float-label {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0.5rem 0;
}

.float-label input {
  height: 64px;
  border-radius: 4px;
  border: 1px solid lightgray;
  padding: 24px 16px 4px 16px;
  font-size: 16px;
  line-height: 1;
  outline: none;
  box-shadow: none;
  transition: 200ms cubic-bezier(0, 0, 0.2, 1) 0ms;
}

.float-label label{
  position: absolute;
  pointer-events: none;
  transform: translate(0, 23px) scale(1);
  transform-origin: top left;
  transition: 200ms cubic-bezier(0, 0, 0.2, 1) 0ms;
  color: #6f81a5;
  font-size: 16px;
  line-height: 1;
  left: 16px;
}

.float-label:focus-within label {
  transform: translate(0, 12px) scale(0.8);
  color: #0a53e4;
}

.float-label .filled {
  transform: translate(0, 12px) scale(0.8);
}

.float-label input:focus{
  box-shadow: 0 0 0 2px #79b1ff;
}

  
input[type=date]:required:invalid::-webkit-datetime-edit {
  color: transparent;
}
input[type=date]:focus::-webkit-datetime-edit {
  color: black !important;
}

